@import 'master.css';

.NominationsList_wrapper {
    display: block;
    margin: 0 auto;
    width: 95%;
}

.NominationsList_heading {
    margin-top: 50px;
    margin-bottom: 0px;
}
.NominationsList_p {
    margin-top: 0px;
}

.NominationsList_ul {
    padding: 0;
    list-style-type: none;
    list-style-image: none;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
}
