@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@900&family=Roboto&family=Work+Sans:wght@200;400;700&display=swap);
:root {
    --main-bg-color: #191c2c;
    --dark-text: #191c2cbb;
    --red: #d7263d;
    --dark-red: #961b2c;
    --light-red: #d83b50;
    --gold: #fcca46;
    --white: #f3f4f6;
    --opal: #a4bab7;
    --dark-opal: #687c7bb6;
    --transparent-white: #f3f4f628;
}
html {
    overflow-y: scroll;
}

body {
    background-color: #191c2c;
    background-color: var(--main-bg-color);
    color: #f3f4f6;
    color: var(--white);
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
}

h1 {
    -webkit-margin-before: 40px;
            margin-block-start: 40px;
    font-size: 90px;
    font-family: 'Work Sans', sans-serif;
}

h2 {
    font-size: 25px;
    font-weight: 400;
}

.flex-center {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.centered-text {
    text-align: center;
}

.red-highlight {
    display: inline-block;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 25px;
    background-image: linear-gradient(90deg, transparent 0%, #fcca46);
    background-image: linear-gradient(90deg, transparent 0%, var(--gold));
    background-position: 0% 110%;
    background-size: auto;
    background-repeat: repeat-x;
    background-size: 200% 50%;
    transition: background-position 0.5s;
}

.show-highlight {
    background-position: -100% 150%;
    transition: background-position 1s;
}

::-webkit-scrollbar {
    background-color: rgba(0, 0, 0, 0);
    margin: 1px;
    width: 0.8em;
}

::-webkit-scrollbar-thumb:window-inactive,
::-webkit-scrollbar-thumb {
    background: rgba(119, 119, 119, 0.658);
    border-radius: 10px;
}
::-webkit-scrollbar-corner {
    background: rgba(0, 0, 0, 0);
}

.NominationDisplay_li {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.NominationDisplay_wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 300px;
    padding: 20px;
}

.NominationDisplay_wrapper:hover {
    background-color: var(--transparent-white);
}

.NominationDisplay_filler {
    display: block;
    height: 300px;
    width: 200px;
    background-color: var(--dark-opal);
}

.NominationsList_wrapper {
    display: block;
    margin: 0 auto;
    width: 95%;
}

.NominationsList_heading {
    margin-top: 50px;
    margin-bottom: 0px;
}
.NominationsList_p {
    margin-top: 0px;
}

.NominationsList_ul {
    padding: 0;
    list-style-type: none;
    list-style-image: none;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 20px;
    gap: 20px;
}

.MSR_li {
    display: 'flex';
    -webkit-align-items: 'center';
            align-items: 'center';
}

.MSR_button {
    width: 100%;
    margin: 0;
    padding: 0;
    border: 0;
    border-top: 1px solid var(--dark-opal);
    display: -webkit-flex;
    display: flex;
    font-family: 'Work Sans', sans-serif;
    color: var(--dark-text);
    font-weight: 200;
    font-size: 18px;
    background-color: var(--white);
}

.MSR_button:hover {
    background-color: var(--opal);
}

.MSR_button:disabled {
    background-color: var(--dark-opal);
}

.SearchBar_wrapper {
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0 auto;
    width: 50%;
}

/* Fixes internal padding / width issue */
input.input {
    width: 100%;
    padding-left: 50px; /* Safari/Chrome, other WebKit */ /* Firefox, other Gecko */
    box-sizing: border-box; /* Opera/IE 8+ */
}

.SearchBar_input {
    display: inline-block;
    margin: 0 auto;
    width: 100%;
    padding: 20px;
    border: none;
    outline: none;
    border-radius: 5px;
    font-size: 25px;
    font-family: 'Work Sans', sans-serif;
    font-weight: 600;
    color: var(--dark-text);
}

.SearchBar_input::-webkit-input-placeholder {
    opacity: 0.5;
    font-style: italic;
}

.SearchBar_input:-ms-input-placeholder {
    opacity: 0.5;
    font-style: italic;
}

.SearchBar_input::placeholder {
    opacity: 0.5;
    font-style: italic;
}

.SearchBar_input:focus {
    outline: none;
    box-shadow: 0 0 3pt 2pt var(--gold);
}

.SearchBar_ul {
    overflow-y: scroll;
    overflow-x: hidden;
    margin: 0 auto;
    padding: 0;
    width: 100%;
    transition: height 0.3s ease;
    top: 400px;
    position: static;
    list-style-type: none;
    list-style-image: none;
}

